ul {
  background-image: linear-gradient(135deg, #20C5A0 0%, #BD10E0 47%, #F5A623 100%);
  padding: 20px !important;
  list-style: none;
  font-size: 12px;
}
ul li {
  width: inline-block;
  text-align: center;
  margin: 0;
  padding: 2px 0;
}

ul li:hover {
  font-weight: bold;
  cursor: pointer;
}

span {
  padding: 4px 2px;
  line-height: 150%;
  border-radius: 4px;
}
.active {
  background: white;
  color: black;
  font-weight: bold;
}
</style>
